import axios from 'axios';
import { stripMarkdown } from './stripMarkdown';

const client = axios.create({
  baseURL: 'https://api.wonsama.dev/v1',
  headers: {
    'Content-Type': 'application/json; charset=utf-8',
  },
});

export type Post = {
  author: string;
  body: string;
  category: string;
  created: string;
  createdAt: string;
  id: number;
  json_metadata: string;
  last_update: string;
  permlink: string;
  title: string;
  updatedAt: string;
  updatedkr: string;
  url: string;
  _id: string;
};

export type GetPostsResponse = {
  count: number;
  limit: number;
  page: number;
  list: Post[];
};

export type GetPostsParams = {
  page?: number;
  limit?: number;
  author: string;
};

export default async function getPosts({
  page = 1,
  limit = 10,
  author,
}: GetPostsParams) {
  return await client
    .get<GetPostsResponse>('/post', {
      params: {
        page,
        limit,
        author,
      },
    })
    .then(async ({ data }) => {
      for (const item of data.list) {
        item.body = String(await stripMarkdown(item.body));
      }
      return data;
    });
}
