import React, { useEffect, useState } from 'react';

import 'normalize.css';

import Container from './components/Container';
import Top from './components/Top';
import Header from './components/Header';
import IconLink from './components/IconLink';
import SubTitle from './components/SubTitle';
import { CardBody, CardFooter } from './components/Card';
import SponsorCard from './components/SponsorCard';
import AboutCard from './components/AboutCard';
import Contents from './components/Contents';
import PostItem from './components/PostItem';
import Footer from './components/Footer';

import getPosts, { Post } from './libs/client';

import config from './config.json';

function App() {
  const [posts, setPosts] = useState<Post[]>();

  useEffect(() => {
    getPosts({ author: config.username }).then((response) => {
      if (response && response.list && response.list.length) {
        setPosts(response.list);
      }
    });
  }, []);

  return (
    <Container>
      <Top />
      <Header username={config.username} />
      <Contents>
        <div className='body-container'>
          <div className='body-panel-1'>
            {config.sponsor.map((sponsor, index) => (
              <SponsorCard
                key={`${sponsor.type}-${index}`}
                username={config.username}
                type={sponsor.type}
                address={sponsor.address}
              />
            ))}
          </div>
          <div className='body-panel-2'>
            <AboutCard>
              <CardBody>
                {config.about.split('\n').map((text) => (
                  <p>{text}</p>
                ))}
              </CardBody>
              <CardFooter>
                <a
                  href={config.homepage}
                  target='_blank'
                  rel='noopener noreferrer'
                >
                  <IconLink />
                </a>
              </CardFooter>
            </AboutCard>
            <SubTitle>Recent Articles</SubTitle>
            {posts
              ?.filter(({ title }) => !title.startsWith('RE: '))
              .slice(0, 5)
              .map((post) => (
                <PostItem key={String(post.id)} post={post} />
              ))}
          </div>
        </div>
      </Contents>
      <Footer />
    </Container>
  );
}

export default App;
